import React from "react";
import Footer from "./Footer";
import AnalysisString from "./AnalysisString";
import { useState, useEffect } from "react";
import AnalysisHistory from "./AnalysisHistory";
import MyPieChart from "./MyPieChart";
import Analysis from "../routes/Analysis";
import "./s2.css";

import {
    PieChart,
    Line,
    XAxis,
    YAxis,
    CartesianGrid,
    Tooltip,
    Legend,
    Pie,
    Cell,
    LineChart,
  } from "recharts";


function BackMode() {
    let ParsedStorage;
    let localStorageKey = [];
    let localStorageValue = [];
    let date;
    let dateChange = [];
    let key;
    let value1;
    const advice = [
        "            !",
        "         ,      ! ",
        "     ! ",
        " 20~30       !",
    ];

    let StorageMap = new Map(); //TODO:
    const [StorageData, setStorageData] = useState([
        {
            XTimeStorage: 0,
            YTimeStorage: 0,
            XVibrateStorage: 0,
            YVibrateStorage: 0,
            Duplicated: 0,
            TotalTimeStorage: 0,
        },
    ]);

    for (let i = 0; i < localStorage.length; i++) {
        if(localStorage.key(i)[0]=='a'){
            const key = localStorage.key(i).substring(1,localStorage.key(i).length); //key a b 
            const value = localStorage.getItem(localStorage.key(i));
            StorageMap.set(key, value);
            //StorageMap  - a  
        }                    
    }
    const SortedStorage = new Map([...StorageMap].sort().reverse()); //
    // console.log(SortedStorage)
    const size = StorageMap.size; // 
    

    for (let i = 0; i < size; i++) {
        localStorageKey[i] = Array.from(SortedStorage.keys())[i];
        localStorageValue[i] = Array.from(SortedStorage.values())[i];
    } //localstorage 
    console.log(localStorageKey)


    for (let i = 0; i < size; i++) {
        localStorageValue[i] = JSON.parse(localStorageValue[i]);
    } //  json  

    function Linechart() {
        let lvalue = [];
        const data01 = [];
        for (let i = 0; i < size; i++) {
        lvalue[i] =
            ((localStorageValue[i].TotalTimeStorage -
            (
                localStorageValue[i].XTimeStorage +
                localStorageValue[i].YTimeStorage -
                localStorageValue[i].Duplicated)) /
                localStorageValue[i].TotalTimeStorage) * 100;
            }
        console.log(lvalue[0]);

        let i = localStorageValue.length;
        let NewCount = 6;
        while (i >= 0 && NewCount >= 0) {
        data01.push({ //data01  , 
            name: localStorageKey[NewCount],
            value: lvalue[NewCount],
        });
        i--;
        NewCount--;
        }
        console.log("data01 : " + data01[0].name);

        return (
        <LineChart
            width={350}
            height={150}
            data={data01}
            margin={{ top: 5, right: 40, left: 0, bottom: 0 }}
        >
            <CartesianGrid strokeDasharray="3 3" />
            <XAxis dataKey="name" />
            <YAxis />
            <Tooltip />
            <Legend />
            <Line
            type="monotone"
            dataKey="value"
            stroke="#8884d8"
            name="   "
            />
        </LineChart>
        );
    }

    return (
        <div>
        <div className="back" style={{ height: "180vh"}}>
          <div className="c">
            <div>            
              <h5 className="e">   ?</h5>              
              <MyPieChart
                Piekey={localStorageKey[0]}
                Pievalue={localStorageValue[0]}
              /> 
              <AnalysisString ParsedStorage={localStorageValue[0]} /> 
            </div>

            <Linechart />
            <hr />
            <p className="d">
              {advice[Math.floor(Math.random() * advice.length)]}
            </p>
            <hr />
            <div>
              <h5 className="e">   ?</h5>
              <AnalysisHistory
                localStorageKey={localStorageKey}
                localStorageValue={localStorageValue}
              />
            </div>
          </div>
          <Footer />
        </div>
      </div>
    )

}

export default BackMode;