import { useSearchParams } from "react-router-dom";
import { useState, useEffect } from "react";
import styles from "./Header.module.css";
import SwitchToggle from "../etc/SwitchToggle";
function Header() {
  const [connected, setConnect] = useState(true); // 
  const [Disconnected, setDisConnect] = useState(true); //  
  const [device, setDevice] = useState(""); //device   :   
  const [Xangle, setXangle] = useState(""); //Xangle 
  const [Yangle, setYangle] = useState(""); //Yangle 
  const [state1, setState1] = useState(" "); //
  const [state2, setState2] = useState(" !"); //
  const [StartTimeState, setStartTimeState] = useState(""); //bluetooth   
  const [EndTimeState, setEndTimeState] = useState(""); //bluetooth   
  const [TotalTimeState, setTotalTimeState] = useState(""); // 
  const [StorageData, setStorageData] = useState([
    {
      XTimeStorage: 0,
      YTimeStorage: 0,
      XVibrateStorage: 0,
      YVibrateStorage: 0,
      Duplicated: 0,
    },
  ]);
  let Xanglevalue = 0;
  let Yanglevalue = 0;
  let CheckError;
  const Storage = {
    XTimeStorage: 0,
    YTimeStorage: 0,
    XVibrateStorage: 0,
    YVibrateStorage: 0,
    Duplicated: 0,
    TotalTimeStorage: 0,
  };
  let Xboolean = false;
  let Yboolean = false;
  let XCount = 0;
  let X40Count = 0;
  let YCount = 0;
  let Y40Count = 0;
  let X40boolean = false;
  let Y40boolean = false;
  let disConnection = false;
  let ConvertedTotalTime = 0;
  let CCount = 0;
  const buffer = [];
  let StartTime = 0;
  let EndTime = 0;
  let TotalTime = 0;
  let ParsedStorage;
  let ConvertedStartTime = 0;
  let ConvertedEndTime = 0;
  let bluetoothDevice;
  let EndTimeByGetTime = 0;
  let StartTimeByGetTime = 0;
  let flagCount = 0;
  let flagCheck;
  let value;
  const [startButton, setStartButton] = useState(false);
  const [flag, setFlag] = useState(true);

  function onClickFlag() {
    setFlag(!flag);
    console.log(flag);
  }

  async function onClickBluetooth() {
    //bluetooth  
    try {
      //   ,      
      const deviceActivate = await navigator.bluetooth.requestDevice({
        filters: [
          {
            services: ["66df5109-edde-4f8a-a5e1-02e02a69cbd5"],
          },
        ],
      });
      bluetoothDevice = deviceActivate;
      StartTime = Date.now();
      let Startdate = new Date(StartTime);
      StartTimeByGetTime = Startdate.getTime();
      ConvertedStartTime = timeConvert(StartTime);
      console.log(ConvertedStartTime);
      setConnect(false);
      setDevice(deviceActivate);
      setStartTimeState(ConvertedStartTime);
      const savedTime = localStorage.getItem(ConvertedStartTime);
      if (savedTime != null) {
        savedTime.forEach((item) => console.log(item));
      }
      console.log("Connecting to GATT Server...");
      bluetoothDevice.addEventListener(
        //   
        "gattserverdisconnected",
        onDisconnected
      );
      // connect(); //  (  )
      const server = await bluetoothDevice.gatt.connect(); // 
      //Service
      const service = await server.getPrimaryService(
        // ,  x y characteristic 
        "66df5109-edde-4f8a-a5e1-02e02a69cbd5"
      );
      //X sensor characteristic
      const characteristic = await service.getCharacteristic(
        "741c12b9-e13c-4992-8a5e-fce46dec0bff"
      );
      //Y sensor characteristic
      const characteristic2 = await service.getCharacteristic(
        "baad41b2-f12e-4322-9ba6-22cd9ce09832"
      );

      const characteristicFlag = await service.getCharacteristic(
        "1a4a954a-494c-11ed-b878-0242ac120002"
      );

      if (flag) {
        //true 
        value = 1;
      } else {
        //false 
        value = 0;
      }

      characteristicFlag.writeValue(new Uint8Array([value]));

      const Xvalue = characteristic.readValue();

      characteristic.addEventListener(
        //X sensor 
        "characteristicvaluechanged",
        handleXangleChanged
      );
      characteristic2.addEventListener(
        //Y sensor 
        "characteristicvaluechanged",
        handleYangleChanged
      );

      const interval = setInterval(() => {
        //1 interval
        bluetoothDevice.addEventListener(
          //  
          "gattserverdisconnected",
          onDisconnected
        );
        if (disConnection) {
          // interval 
          clearInterval(interval);
          disConnection = false;
        }
        CCount++;
        const Xvalue = characteristic.readValue(); //Xsensor   (  characteristicvaluechanged  EventListener )
        const Yvalue = characteristic2.readValue(); //Ysensor  

        //handleXangleChanged , handleYangleChanged  Count 
        if (XCount >= 7 || YCount >= 7) {
          //3  X Y  
          setState1("  !"); //
          setState2("    !"); //
          if (X40boolean == true || Y40boolean == true) {
            setState1("   !! ");
            setState2("    ! ");
          } //
        } else {
          setState1(" "); //
          setState2(" !"); //
        }
        if (Xboolean == true && Yboolean == true) {
          Storage.Duplicated++;
        }
        if (value == 1) {
          //
          localStorage.setItem(ConvertedStartTime, JSON.stringify(Storage));
          const SavedStorage = localStorage.getItem(ConvertedStartTime);
          if (SavedStorage != null) {
            ParsedStorage = JSON.parse(SavedStorage);
          }
          setStorageData((prevState) => ParsedStorage);
        } else {
          localStorage.setItem(
            "a" + ConvertedStartTime,
            JSON.stringify(Storage)
          );
          const SavedStorage = localStorage.getItem("a" + ConvertedStartTime);
          if (SavedStorage != null) {
            ParsedStorage = JSON.parse(SavedStorage);
          }
          setStorageData((prevState) => ParsedStorage);
        }
      }, 1000);
    } catch (error) {
      console.log("Argh! " + error);
    }
  }
  function hex2a(hexx) {
    //hex to Dec 
    let hex = hexx.toString(); //force conversion
    let str = "";
    for (let i = 0; i < hex.length; i += 2)
      str += String.fromCharCode(parseInt(hex.substr(i, 2), 16));
    return str;
  }
  function handleXangleChanged(event) {
    //X  
    //16 10 
    console.log("EVent");
    console.log(event);
    console.log(event.target.value);
    const length = event.target.value.byteLength;
    for (let i = 0; i < length; i++) {
      buffer[i] = event.target.value.getUint8(i).toString(16);
    }
    const bufferMerge = buffer.join("");
    Xanglevalue = hex2a(bufferMerge);
    setXangle(Xanglevalue); //Xangle useState 
    if (value == 1) {
      // 
      if (Xanglevalue > 15 || Xanglevalue < -15) {
        // 
        XCount++; //1 interval ,  1  +1
        Storage.XTimeStorage++;
        Xboolean = true;
        if (Xanglevalue > 40 || Xanglevalue < -40) {
          X40boolean = true;
        } else {
          X40boolean = false;
        }
        if (XCount >= 7) {
          //3   
          if (XCount == 7) {
            Storage.XVibrateStorage++;
          }
          console.log("7 Y value  :  ");
        }
      } else {
        // 
        XCount = 0; //
        X40Count = 0;
        Xboolean = false;
      }
    } // 
    else {
      if (Xanglevalue < -15) {
        //TODO:  X
        // 
        XCount++; //1 interval ,  1  +1
        Storage.XTimeStorage++;
        Xboolean = true;
        if (Xanglevalue < -40) {
          X40boolean = true;
        } else {
          X40boolean = false;
        }
        if (XCount >= 7) {
          //3   
          if (XCount == 7) {
            Storage.XVibrateStorage++;
          }
          console.log("7 Y value  :  ");
        }
      } else {
        // 
        XCount = 0; //
        X40Count = 0;
        Xboolean = false;
      }
    }
  }
  // , Y sensor 
  function handleYangleChanged(event) {
    const length = event.target.value.byteLength;
    for (let i = 0; i < length; i++) {
      buffer[i] = event.target.value.getUint8(i).toString(16);
    }
    const bufferMerge = buffer.join("");
    Yanglevalue = hex2a(bufferMerge);
    setYangle(Yanglevalue);
    if (value == 1) {
      //  
      if (Yanglevalue > 10 || Yanglevalue < -10) {
        YCount++;
        Yboolean = true;
        Storage.YTimeStorage++;
        if (Yanglevalue > 40 || Yanglevalue < -40) {
          Y40boolean = true;
        } else {
          Y40boolean = false;
        }
        if (YCount >= 7) {
          if (YCount == 7) {
            Storage.YVibrateStorage++;
          }
          console.log("7 Y value  :  ");
        }
      } else {
        YCount = 0;
        Y40Count = 0;
        Yboolean = false;
      }
    } // 
    else {
      if (Yanglevalue < -15) {
        // y
        YCount++;
        Yboolean = true;
        Storage.YTimeStorage++;
        if (Yanglevalue < -40) {
          Y40boolean = true;
        } else {
          Y40boolean = false;
        }
        if (YCount >= 7) {
          if (YCount == 7) {
            Storage.YVibrateStorage++;
          }
          console.log("7 Y value  :  ");
        }
      } else {
        YCount = 0;
        Y40Count = 0;
        Yboolean = false;
      }
    }
  }

  //  
  async function onDisconnected() {
    disConnection = true;
    setDisConnect(false);
    EndTime = Date.now();
    let EndDate = new Date(EndTime);
    EndTimeByGetTime = EndDate.getTime();
    ConvertedEndTime = timeConvert(EndTime);
    TotalTime = (EndTimeByGetTime - StartTimeByGetTime) / 1000; // 
    Storage.TotalTimeStorage = TotalTime;
    localStorage.setItem(ConvertedStartTime, JSON.stringify(Storage));
    setEndTimeState(ConvertedEndTime);
    setTotalTimeState(TotalTime);
    await device.gatt.disconnect(); //
  }
  function time(text) {
    console.log("[" + new Date().toJSON().substr(11, 8) + "] " + text);
  }
  // 
  function timeConvert(time) {
    let date = new Date(time);
    let year = date.getFullYear().toString().slice(-2); //  
    let month = ("0" + (date.getMonth() + 1)).slice(-2); // 2 (01, 02 ... 12)
    let day = ("0" + date.getDate()).slice(-2); // 2 (01, 02 ... 31)
    let hour = ("0" + date.getHours()).slice(-2); // 2 (00, 01 ... 23)
    let minute = ("0" + date.getMinutes()).slice(-2); // 2 (00, 01 ... 59)
    let second = ("0" + date.getSeconds()).slice(-2); // 2 (00, 01 ... 59)
    let returnDate =
      year +
      "." +
      month +
      "." +
      day +
      ". " +
      hour +
      ":" +
      minute +
      ":" +
      second;
    return returnDate;
  }

  return (
    <div className={styles.row}>
      <SwitchToggle />
      <button onClick={onClickFlag}>
        {" "}
        {flag ? "  " : "  "}{" "}
      </button>
      <button onClick={onClickBluetooth}>Click to connect bluetooth</button>
      {connected ? (
        <h4>Device Loading...</h4>
      ) : (
        <h4>Device name : {device.name}</h4>
      )}
      <br></br>
      <div className={styles.container}>
        <h2 className={styles.xy}>XAngle : {Xangle} </h2>
        <br></br>
        <h2 className={styles.xy}>YAngle : {Yangle} </h2>
      </div>
      <div className={styles.TextBox}>
        {connected ? (
          ""
        ) : Disconnected ? (
          <div>
            <h3 className={styles.s1}>{state1}</h3>
            <p className={styles.s2}>{state2}</p>
          </div>
        ) : (
          ""
        )}{" "}
        {/*  */}
        {connected ? (
          <div>
            <br></br>
            <p className={styles.defaultHeaderRender}>
                  !{" "}
            </p>
          </div>
        ) : (
          <div>
            <br></br>
            <p className={styles.defaultHeaderRender}>
               : {StartTimeState}
            </p>
          </div>
        )}
        {Disconnected ? (
          ""
        ) : (
          <p className={styles.defaultHeaderRender}>
             : {EndTimeState}
          </p>
        )}
        {Disconnected ? (
          ""
        ) : (
          <p className={styles.defaultHeaderRender}>
               : {TotalTimeState}
          </p>
        )}
        {connected ? (
          ""
        ) : (
          <div className={styles.defaultHeaderRender}>
            <p>
                 :
              {StorageData.XTimeStorage +
                StorageData.YTimeStorage -
                StorageData.Duplicated}{" "}
              
            </p>
            <p>
                :{" "}
              {StorageData.XVibrateStorage + StorageData.YVibrateStorage}
            </p>
          </div>
        )}
      </div>
    </div>
  );
}
export default Header;
